import React from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <div
      style={{
        backgroundColor: "#274167",
        display: "flex",
        gap: "50px",
        justifyContent: "center",
       
      }}
    >
      <Link to="/">
        <button
          style={{
            height: "40px",
            width: "250px",
            backgroundColor: "transparent",
            border: "none",
            color: "white",
            cursor: "pointer",
            fontSize: "25px",
          }}
        >
          Registration Form
        </button>
      </Link>
      <Link to='/table'>
      <button
        style={{
          height: "40px",
          width: "250px",
          backgroundColor: "transparent",
          border: "none",
          color: "white",
          cursor: "pointer",
          fontSize: "25px",
        }}
      >
        Student Information
      </button>
      </Link>
    
    </div>
  );
};

export default Navbar;
