import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useLocation, useNavigate } from "react-router-dom";
import infoImage from "../../Components/Images/information.png";
import crossImage from "../Images/cross.png";
import searchIcon from "../Images/searchIcon.png";
import moment from "moment";
import axios from "axios";
import ClipLoader from "react-spinners/ClipLoader";
import "./Table.css";
const Table = () => {
  const navigator = useNavigate();
  const [loadingInProgress, setLoading] = useState(false);

  const params = useLocation();
  console.log(params?.state, "asdknasjhkdasjdkjkasdjk");

  const [data, setData] = useState([]);
  const [filteredList, setFilteredList] = useState([]);
  let [query, setQuery] = useState("");
  let [time, setTime] = useState([]);

  let [searchParam] = useState([
    "firstName",
    "lastName",
    "email",
    "phonenumber",
    // "guardianNumber",
    // "emergencyNumber",
    "cnic",
    // "city",
    // "address",
    "studentId",
    // "entryYear",
    // "university",
    // "department",
    // "smester",
    // "roomNumber",
    // "floorNumber",
    // "rentAmount"
  ]);

  useEffect(() => {
    getStudents();
  }, []);

  // console.log(,"jhvghv ghb")

  // let query;

  const getStudents = () => {
    axios
      .get("http://localhost:7000/students")
      .then((data) => {
        console.log(data.data);
        setData(data.data);
      })
      .catch((error) => console.log(error));
    console.log("DATA GEt");
  };

  function search(items) {
    return items.filter((item) => {
      return searchParam.some((newItem) => {
        return (
          item[newItem].toString().toLowerCase().indexOf(query.toLowerCase()) >
          -1
        );
      });
    });
  }

  const studentEntryTime = (id) => {

    axios
      .post("http://localhost:7000/studentEntryTime/" + id, {
        // "userId":id,
        date: new Date().toDateString(),
        entryTime: new Date(),
        exitTime: "",
      })
      .then((data) => {
        setTime((oldArray) => [...oldArray, data.data]);
        console.log(data.data);
      })
      .catch((error) => console.log(error));
    console.log("DATA SEND");
  };

  const studentExitTime = (id) => {
    // console.log(time);
    // // console.log(new Date().toDateString() + "asdjasjdnjnj");
    axios
      .post("http://localhost:7000/studentExitTime/" + id, {
        //     // "userId":id,
        date: new Date().toDateString(),
        exitTime: new Date(),    
      })
      .then((data) => {
        setTime((oldArray) => [...oldArray, data.data]);
        console.log(data);
      })
      .catch((error) => console.log(error));
    console.log("DATA SEND");
  };

  const studentDetails = (studentDetail) => {
    navigator("/timeDetails", { state: studentDetail });
  };

  const clearQuery = () => {
    query = "";
    setQuery("");
    console.log(query);
  };

  return (
    <div style={{ backgroundColor: "#e1e1e1", height: "100vh" }}>
      <h1
        style={{
          display: "flex",
          justifyContent: "center",
          paddingTop: "50px",
          fontSize: "50px",
        }}
      >
        <span style={{ color: "#252525" }}>Student Data</span>
      </h1>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          padding: "20px",
        }}
      >
        <div>
          <Link to="/StudentDataForm">
            <button className="add-btn">Add</button>
          </Link>
        </div>
        <div style={{ position: "relative" }}>
          <input
            style={{
              height: "40px",
              width: "250px",
              borderRadius: "10px",
              paddingLeft: "10px",
              fontSize: "20px",
            }}
            type="text"
            id="text"
            placeholder="Search Here"
            value={query}
            onChange={(e) => {
              query = e.target.value;
              setQuery(e.target.value);
              console.log(e.target.value);
            }}
          />
          <div className="searchIcon">
            <img
              className="searchImage"
              src={query ? crossImage : searchIcon}
              alt="image"
              onClick={query ? clearQuery : null}
            />
          </div>
        </div>
      </div>
      <div>
        <table>
          <thead>
            <tr>
              <th>First Name</th>
              <th>Last Name</th>
              {/* <th> Email</th> */}
              {/* <th> Phone Number</th> */}
              {/* <th> Guardian Number</th> */}
              {/* <th> Emergency Number</th> */}
              <th>CNIC</th>
              <th>City</th>
              {/* <th> Address</th> */}
              <th> Student ID</th>
              {/* <th> Entry Year</th> */}
              {/* <th> University</th> */}
              <th> Department</th>
              {/* <th>Semester</th> */}
              <th>Room Number</th>
              <th>Floor Number</th>
              {/* <th>Rent Amount</th> */}
              <th>Entry Time </th>
              <th>Exit Time</th>
              <th>Student Detail</th>
            </tr>
          </thead>
          <tbody>
            {search(data).map((e) => {
              return (
                <tr>
                  <td>{e.firstName}</td>
                  <td>{e.lastName}</td>
                  {/* <td>{e.email}</td> */}
                  {/* <td>{e.phonenumber}</td> */}
                  {/* <td>{e.guardianNumber}</td> */}
                  {/* <td>{e.emergencyNumber}</td> */}
                  <td>{e.cnic}</td>
                  <td>{e.city}</td>
                  {/* <td>{e.address}</td> */}
                  <td>{e.studentId}</td>
                  {/* <td>{e.entryYear}</td> */}
                  {/* <td>{e.university}</td> */}
                  <td>{e.department}</td>
                  {/* <td>{e.smester}</td> */}
                  <td>{e.roomNumber}</td>
                  <td>{e.floorNumber}</td>
                  {/* <td>{e.rentAmount}</td> */}
                  <td>
                    <button
                      className="entry-btn"
                      disabled={
                        time.length === 0
                          ? e.entryTime
                            ? true
                            : false
                          : time.some(
                              (myTime) =>
                                e._id === myTime.userId ||
                                myTime.entryTime != ""
                            )
                          ? true
                          : false
                      }
                      onClick={() => {
                        studentEntryTime(e._id);
                      }}
                      type="button">
                      {!e.entryTime
                        ? time.length === 0
                          ? "NO"
                          : time.map((myTime) =>
                              e._id === myTime.userId
                                ? moment(myTime.entryTime).format("hh:mm:a")
                                : "NO"
                            )
                        : moment(e.entryTime).format("hh:mm:a")}
                    </button>
                  </td>
                  <td>
                    <button
                      className="exit-btn"
                      disabled={
                        time.length === 0
                          ? e.exitTime
                            ? true
                            : false
                          : time.some(
                              (MyTime) =>
                                e._id === MyTime.userId || MyTime.exitTime != ""
                            )
                          ? true
                          : false
                      }
                      onClick={() => {
                        studentExitTime(e._id);
                      }}
                    >
                      {!e.exitTime
                        ? time.length === 0
                          ? "NO"
                          : time.map((MyTime) =>
                              e._id === MyTime.userId
                                ? moment(MyTime.exitTime).format("hh:mm:a")
                                : "NO"
                            )
                        : moment(e.exitTime).format("hh:mm:a")}
                    </button>
                  </td>
                  <td>
                    <img
                      onClick={() => {
                        studentDetails(e);
                      }}
                      src={infoImage}
                      alt="image"
                      style={{ height: "5vh", cursor: "pointer" }}
                      data={data}
                    />
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Table;
