import React, { useform, yupResolver } from "react";
import { useLocation } from "react-router-dom";
import swal from "sweetalert";
import "./RentDetails.css";
import axios from "axios";

const RentDetails = () => {
  // new Date().toLocaleString()

  const params = useLocation();
  console.log(params?.state, "asdknasjhkdasjdkjkasdjk");

  const selectedRoom = (e) => {
    console.log(e.target.value);
    params.state.roomNumber = e.target.value;
  };

  const selectFloor = (e) => {
    console.log(e.target.value);
    params.state.floorNumber = e.target.value;
  };

  const submitDetails = () => {
    params.state.rentAmount = _rentAmount;

    postUserData();
  };

  let roomsList = ["01", "02", "03", "04", "05"];

  let _rentAmount = 0;

  let floorList = ["01", "02", "03", "04", "05"];

  ////////   SAVING DATA TO DB  ///////

  const postUserData = () => {
    console.log("hwereew");
    axios
      .post("http://localhost:7000/studentRegistration", params.state)
      .then((data) => {
        console.log(data);
        if (data.data === "Success") {
          swal(
            "Congratulation!",
            "Student is Registered Successfully",
            "success"
          );
        } else {
          swal("Error!", `Something went Wrong! due to ${data.data}`, "error");
        }
      })

      .catch((error) => console.log(error));
    console.log("DATA SEND");
  };

  return (
    <>
      <div style={{ backgroundColor: "#e1e1e1", height: "100vh" }}>
        <div
          style={{
            fontSize: "50px",
            display: "flex",
            justifyContent: "center",
            paddingTop: "50px",
          }}
        >
          <p>Rent Details</p>
        </div>
        <div style={{ paddingTop: "30px",alignItems:"center" }}>
          <hr></hr>
        </div>
        <form>
          <div
            style={{
              display: "flex",
              borderRadius: "20px",
              justifyContent: "center",
              marginTop: "100px",
            }}
          >
            <div
              style={{
                backgroundColor: "#F6F6F6",
                padding: "20px",
                borderRadius: "20px",
              }}
            >
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                <div>
                  <select
                    style={{
                      border: "none",
                      color: "#252525",
                      height: "40px",
                      width: "220px",
                      fontSize:"15px"
                    }}
                    onChange={selectedRoom}
                  >
                    <option select value="selectRoomNumber">
                      Select Room Number
                    </option>
                    {roomsList.map((e) => {
                      return <option value={e}>{e}</option>;
                    })}
                  </select>
                </div>
                <div>
                  <select
                    style={{
                      border: "none",
                      color: "#252525",
                      height: "40px",
                      width: "200px",
                      fontSize:"15px"
                    }}
                    onChange={selectFloor}
                  >
                    <option select value="selectFloor">
                      Select Floor
                    </option>
                    {floorList.map((e) => {
                      return <option value={e}>{e}</option>;
                    })}
                  </select>
                </div>
              </div>
              <div
                style={{
                  paddingTop: "50px",
                  display: "flex",
                  flexDirection: "column",
                }}
              >
                <label
                  style={{ paddingBottom: "10px", color: "#252525",fontSize:"25px" }}
                  for="rentamount"
                >
                  Rent Amount
                </label>
                <input
                  style={{ height: "30px", width: "500px", border: "none",fontSize:"25px" }}
                  type="number"
                  id="rentamount"
                  onChange={(data) => {
                    _rentAmount = data.target.value;
                  }}
                />
              </div>
              <button
                onClick={submitDetails}
                className="submit-btn"
                type="button"
              >
                Submit
              </button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};

export default RentDetails;
