import React, { useEffect, useState } from "react";
import "./TimeDetails.css";
import { useLocation,Link } from "react-router-dom";
import axios from "axios";
import ClipLoader from "react-spinners/ClipLoader";
import moment from "moment";


const TimeDetails = () => {
  const [loadingInProgress, setLoading] = useState(true);
  const [timeHsitory, setTimeHistory] = useState(null);
  useEffect(() => {
    getTimeHistory(params.state._id);
  }, []);

  const params = useLocation();

  const getTimeHistory = (id) => {
    setLoading(true);
    axios
      .get("http://localhost:7000/getTimeHistory/" + id)
      .then((data) => {
        console.log(data.data.dateHistory[0].date);
        setTimeHistory(data.data);
        setLoading(false);
        // if(data != null){
        // }else{
        //   setLoading(false);
        // }
      })
      .catch((error) => console.log(error));
    console.log("DATA GEt");
    setLoading(false);
  };

  const openWhatsapp = ()=>{
    const URL = "https://api.whatsapp.com/send/?phone=+923084695012&text='Arham here'";
    window.open(URL);
  }
  
  return (
    <div style={{ backgroundColor: "#e1e1e1", height: "150vh" }}>      

      <button onClick={openWhatsapp} >WHATSAPP</button>
      <h1
        style={{
          display: "flex",
          justifyContent: "center",
          paddingTop: "30px",
          fontSize: "50px",
        }}
      >
        Student Details
      </h1>
      <div>
        <Link to='/Table'>
      <button className="back-btn">back</button>
        </Link>
      </div>
      <div style={{ width: "80%", paddingLeft: "150px", paddingTop: "30px" }}>
        <hr></hr>
      </div>
      <div
        style={{
          marginLeft: "30px",
          marginTop: "40px",
          fontSize: "20px",
          display: "flex",
          justifyContent: "center",
        }}
      >
        <div
          style={{
            backgroundColor: "#F6F6F6",
            borderRadius: "30px",
            padding: "20px",
            width: "55vw",
          }}
        >
          <div style={{ display: "flex", gap: "150px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>First Name :</b>
            </li>
            <p>{params.state.firstName}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "153px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Last Name :</b>
            </li>
            <p>{params.state.lastName}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "197px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Email :</b>
            </li>
            <p>{params.state.email}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "113px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Phone Number :</b>
            </li>
            <p>{params.state.phonenumber}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "84px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Guardian Number :</b>
            </li>
            <p>{params.state.guardianNumber}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "67px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Emergency Number :</b>
            </li>
            <p>{params.state.emergencyNumber}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "203px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>CNIC :</b>
            </li>
            <p>{params.state.cnic}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "212px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>City :</b>
            </li>
            <p>{params.state.city}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "176px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Address :</b>
            </li>
            <p
              className="address"
              // style={{
              //   width: "25vw",
              //   textOverflow: "ellipsis",
              //   display: "-webkit-box",
              //   WebkitLineClamp: "2px",
              //   lineClamp: "2px",
              //   WebkitBoxOrient: "vertical",
              //   backgroundColor: "red",
              //   overflow: "hidden",
              //   height:"5rem"
              // }}
            >
              {params.state.address}
            </p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "150px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Student ID :</b>
            </li>
            <p>{params.state.studentId}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "150px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Entry Year :</b>
            </li>
            <p>{params.state.entryYear}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "152px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>University :</b>
            </li>
            <p>{params.state.university}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "134px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Department :</b>
            </li>
            <p> {params.state.department}</p>
            <br />
          </div>
          <div style={{ display: "flex", gap: "170px", alignItems: "center" }}>
            <li style={{ fontSize: "20px" }}>
              <b>Smester :</b>
            </li>
            <p>{params.state.smester}</p>
            <br />
          </div>
        </div>
      </div>
      <div style={{ display: "flex", justifyContent: "space-around" }}>
        <div style={{ display: "flex", flexDirection: "column" }}>
          <div
            style={{
              marginTop: "90px",
              fontSize: "20px",
              marginLeft: "150px",
            }}
          >
            <h1>Time Details</h1>
          </div>
          <div
            style={{
              marginTop: "30px",
              marginLeft: "30px",
            }}
          >
            {!timeHsitory ? (
              <div className="loader-container">
                <ClipLoader color={"#252525"} size={50} />
              </div>
            ) : 
              <table>
                <thead style={{ backgroundColor: "rgb(195, 195, 195)" }}>
                  <tr>
                    <th>
                      <b>Date</b>
                    </th>
                    <th>
                      <b>Entry Time</b>
                    </th>
                    <th>
                      <b>Exit Time</b>
                    </th>
                  </tr>
                </thead>
                <tbody>
                {console.log(timeHsitory.dateHistory, 'timeHsitory.')}
                  {timeHsitory.dateHistory.map((e) => {

                    return (
                      <tr>
                        <td>{e.date}</td>
                        <td>{moment(e.entryTime).format("hh:mm:a")} </td>
                        <td>
                          {e.exitTime
                            ? moment(e.exitTime).format("hh:mm:a")
                            : ""}
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            }
          </div>
        </div>
        <div style={{ display: "flex", flexDirection: "column" }}>
          <div
            style={{
              marginTop: "90px",
              fontSize: "20px",
              marginLeft: "150px",
            }}
          >
            <h1>Rent Details</h1>
          </div>
          <div
            style={{
              marginTop: "30px",
              marginRight: "30px",
            }}
          >
            <table>
              <thead style={{ backgroundColor: "rgb(195, 195, 195)" }}>
                <tr>
                  <th>
                    <b>Month</b>
                  </th>
                  <th>
                    <b>Amount</b>
                  </th>
                  <th>
                    <b>Status</b>
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>hkajshdjk</td>
                  <td>hkajshdjk</td>
                  <td>hkajshdjk</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default TimeDetails;
